import { createServer } from "http";
import { readFileSync } from "fs";
import { resolve } from "path";
import { mimes } from "./mimes";
import React from "react";
import ReactDOMServer from "react-dom/server";
import App from "../pages/App";
import { StaticRouter } from "react-router";
(function () {
    var basepath = resolve(__dirname);
    var server = createServer(function (request, response) {
        try {
            var url = request.url;
            var pathname = url || "";
            if (!url || url === "" || url === "/" || url.match(/^[^\.]+$/)) {
                pathname = "index.html";
                var data = readFileSync(pathname, "utf-8");
                response.writeHead(200, { "Content-Type": mimes["html"] });
                response.write(data.replace('<div id="root"></div>', "<div id=\"root\">" + ReactDOMServer.renderToString(React.createElement(StaticRouter, { context: {}, location: request.url },
                    React.createElement(App, null))) + "</div>"));
                response.end();
            }
            else {
                var filename = pathname.substring(1, pathname.length);
                var ext = filename.match(/(?<=\.)[^\.]+$/);
                var contentType = ext && ext.length > 0 && mimes[ext[0]]
                    ? mimes[ext[0]]
                    : "text/plain";
                // console.log(filename, ext, contentType);
                var data = readFileSync(filename);
                response.writeHead(200, { "Content-Type": contentType });
                response.write(data);
                response.end();
            }
        }
        catch (e) {
            console.log(e);
            response.writeHead(500);
            response.end();
        }
    });
    var port = 8092;
    server.listen(port);
    server.on("listening", function () {
        console.log("Server listening on " + port);
    });
    server.on("error", function () {
        server.listen(++port);
    });
})();
