import React from 'react';
import { Route } from 'react-router-dom';
import IndexPage from './index';
import AuthPage from './auth';
import LoginPage from './auth/login';
import RegisterPage from './auth/register';
import EditPage from './edit';
import Page404 from './404';
var App = function (props) {
    return (React.createElement("div", { className: "App" },
        React.createElement(Route, { path: "/", component: IndexPage, exact: true }),
        React.createElement(Route, { path: "/auth", component: AuthPage }),
        React.createElement(Route, { path: "/auth/login", component: LoginPage }),
        React.createElement(Route, { path: "/auth/register", component: RegisterPage }),
        React.createElement(Route, { path: "/edit", component: EditPage }),
        React.createElement(Route, { nomatch: true, path: "/404", component: Page404 })));
};
export default App;
